



























































.container {
  width: 100vw;
  background: #f3f3f3;
  min-height: calc(100vh - 100px);
  padding-bottom:100px;
  .header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-image: url("https://s0.meituan.net/bs/file/?f=my-canary:deploy/dpmmweb/client/dpmmweb/component/mycenter/img/bg.png");
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: 100% 300px;
    height: 300px;
    overflow: hidden;
    text-align: center;
    .avatar {
      width: 120px;
      height: 120px;
      border-radius: 120px;
      border: 1px solid #fff;
      overflow: hidden;
    }
    .name {
      height: 45px;
      margin-top: 14px;
      font-size: 32px;
      line-height: 45px;
      color: #fff;
    }
  }
}
.order {
  margin-top: 20px;
  background: #fff;
  padding: 26px 30px 0;
  .title-box {
    position: relative;
    width: 320px;
    height: 42px;
    margin: 0 auto;
    .title {
      position: relative;
      height: 42px;
      width: 160px;
      margin: 0 auto;
      font-size: 32px;
      line-height: 42px;
      color: #333;
      text-align: center;
      background: #fff;
      z-index: 9;
    }

    .title-line {
      position: absolute;
      width: 320px;
      height: 1px;
      left: 0;
      top: 23px;
      background: #f0f0f0;
    }
  }
}

.order-list {
  display: flex;
  height: 230px;
  align-items: center;
  background: #fff;
}

.order-item {
  width: 50%;
  text-align: center;
  img {
    width: 84px;
    height: 84px;
    margin-bottom: 6px;
  }
  div {
    color: #333;
    font-size: 28px;
  }
}

.other {
  margin-top: 20px;
  background: #fff;
  .other-item {
    position: relative;
    height: 100px;
    line-height: 100px;
    font-size: 30px;
    margin-left: 30px;
    padding-right: 30px;
    color: #333;
    border-bottom: 1px solid #f0f0f0;
    &::after {
      position: absolute;
      right: 30px;
      top: 50%;
      content: "";
      width: 16px;
      height: 16px;
      border-left: 1px solid #ccc;
      border-top: 1px solid #ccc;
      transform: translateY(-50%) rotate(135deg);
    }
  }
  .btn {
    width: 100%;
    border: none;
    border-radius: 0;
    padding: 0;
    margin: 0;
    font-size: 1em;
    position: static;
    line-height: normal;
    color: #333;
    text-align: left;
    background: #fff;
    &::after {
      content: none;
    }
  }
  .iconfont {
    display: inline-block;
    line-height: 100px;
    margin-right: 8px;
    font-size: 32px;
    color: #333;
  }
}

.customer {
  .iconfont {
    font-size: 38px;
  }
}

.about {
  .iconfont {
    opacity: 0.8;
  }
}
.loginOut {
  text-align: center;
  background: #e54847;
  color: #fff;
  padding: 15px 10px;
  width: 90vw;
  margin: 10vw auto;
}
